import Vue from 'vue'
import Router from 'vue-router'
import Login from '../components/Login'
import Register from '../components/Register'
import Index from '../components/Index'
import Change from "../components/Change"


Vue.use(Router);

export const routes=[
  {
    path: '/',
    name:Index,
    component:Index
  },
  {
    path: '/login',
    name:Login,
    component: Login
  },
  {
    path:'/register',
    name:Register,
    component:Register
  },
  {
    path:'/retrieve',
    name:Change,
    component:Change
  }];




