<template>
  <div id="app">
    <header>
      <div class="logo"><router-link to="/"><img src="./assets/logo.png"/></router-link></div>
      <div class="title">SHOPZJU</div>
      <div class="loginUser">
        <div class="logAndReg" v-show="!isLogin">
          <router-link to="/login" >登录</router-link>
          <router-link to="/register">注册</router-link>
        </div>
        <div class="userHome" v-show="isLogin">
          <router-link to="/login">[退出】</router-link>
        </div>
      </div>
    </header>
    <div class="container">
      <router-view/>
    </div>
  </div>
</template>

<script>
  export default {
  name: 'App',
  methods:{

  },
  computed:{
    currentUser(){
      return this.$store.getters.currentUser;
    },
    isLogin(){
      if(sessionStorage.getItem("userName")&&sessionStorage.getItem("userToken")){
        this.$store.commit("userStatus",sessionStorage.getItem("userName"));
      }else{
        this.$store.commit("userStatus",null);
      }
      return this.$store.getters.isLogin;
    }
  }
}
</script>

<style>
  header{
    width:100%;
    height:70px;
    background-color: #ff0000;
  }
  .logo{
    float:left;
    width:15%;
    height:70px;
    padding:5px;
  }
  .logo img{
    cursor: pointer;
    display:block;
    height: 60px;
  }
  .title{
    font-family: 'Droid Serif';
    height:70px;
    color:white;
    font-size:2em;
    position:absolute;
    left:50%;
    transform: translateX(-50%);
    letter-spacing: 10px;
    cursor:default;
    padding-top:10px;
  }
  .loginUser{
    float:right;
    padding:40px 10px 0 0;
  }
  .loginUser .logAndReg a{
    color: #cccccc;
    font-size: 18px;
    cursor: pointer;
    text-decoration: none;
  }
</style>



